/**
 * hornet-js-react-components - Ensemble des composants web React de base de hornet-js
 *
 * @author MEAE - Ministre de l'Europe et des Affaires trangres
 * @version v5.1.0
 * @link git+https://github.com/diplomatiegouvfr/hornet-js.git
 * @license CECILL-2.1
 */

import * as React from "react";
import { HornetComponentProps, HornetComponentState } from "hornet-js-components/src/component/ihornet-component";
import { HornetComponent } from "src/widget/component/hornet-component";
import * as classNames from "classnames";
import ReactDOM = __React.ReactDOM;

/**
 * Proprits Button
 */
export interface ButtonProps extends HornetComponentProps {
    type?: string;
    id?: string;
    name?: string;
    value?: string;
    onClick?: React.MouseEventHandler<HTMLElement>;
    className?: string;
    label?: string;
    title?: string;
    disabled?: boolean;
    url?: string;
    /** Indicateur d'ouverture d'un popup suite  clic sur bouton */
    hasPopUp?: boolean;
}

export interface ButtonState extends ButtonProps, HornetComponentState {
    type?: string;
    effect?: boolean;
    css?: any;
    unmount?: boolean;
}

/**
 * Composant Button
 */
export class Button<P extends ButtonProps, S extends ButtonState> extends HornetComponent<ButtonProps, ButtonState> {

    static defaultProps = {
        disabled: false
    };

    public readonly props: Readonly<ButtonProps>;
    public readonly state: ButtonState;

    constructor(props, context?: any) {
        super(props, context);

        this.state.effect= false;
        this.state.css= {dislay: "none"};

    }

    /**
     * @inheritDoc
     */
    render(): JSX.Element {

        return (
            (this.state.type === "link") ? this.renderLink() : this.renderButton()
        );
    }

    /**
     * Rendu type Button
     * @returns {any}
     * @private
     */
    private renderButton(): JSX.Element {

        let classes: ClassDictionary = {};
        if (this.state.className) {
            classes[this.state.className] = true;
        }

        return (
            <button
                type={this.state.type}
                id={this.state.id}
                name={this.state.name}
                value={this.state.value}
                onClick={this.handleClick}
                className={classNames(classes)}
                title={this.state.title}
                disabled={this.state.disabled}
                aria-haspopup={this.props.hasPopUp}>
                {this.state.label}
                {this.state.effect ? <div className="ripple-effect" style={this.state.css}/> : null}
            </button>
        );
    }

    /**
     * Rendu Type Link
     * @returns {any}
     * @private
     */
    private renderLink(): JSX.Element {

        let classes: ClassDictionary = {};
        if (this.state.className) {
            classes[this.state.className] = true;
        }

        return (
            <a href={this.state.url}
               className={classNames(classes)}
               title={this.state.title}
               onClick={this.linkHandleClick}
               disabled={this.state.disabled}
            >
                {this.state.label}
                {this.state.effect ? <div className="ripple-effect" style={this.state.css}/> : null}
            </a>
        );
    }

    /**
     * Evnement dclench lors du clic sur le bouton
     * @param e
     * @private
     */
    private handleClick(e): void {
        if (this.state.onClick != null) {
            this.state.onClick(e);
        }
        this.rippleEffect(e);

    }

    private linkHandleClick(e): void {
        this.rippleEffect(e);
    }

    /**
     * Effet activ lors d'un lcick sur un bouton
     * @param e
     */
    rippleEffect(e) {
        let xPos = e.pageX - e.target.offsetLeft;
        let yPos = e.pageY - e.target.offsetTop;
        let size = e.target.clientHeight;
        let css = ({
            top: yPos - (size / 2),
            left: xPos - (size / 2),
            height: size + "px",
            width: size + "px"
        });

        this.setState({effect: true, css: css});
        setTimeout(() => {
            if (this.mounted) {
                this.setState({effect: false});
            }
        }, 1500);
    }

    componentWillUnmount() {
        super.componentWillUnmount();
        this.mounted = false;
    }
}


