import { Utils } from "hornet-js-utils";
import { Logger } from "hornet-js-utils/src/logger";
import * as React from "react";

import { AbstractBodyCell, AbstractBodyCellProps } from "src/widget/table/column/cell/abstract-body-cell";

export interface BodyCellProps extends AbstractBodyCellProps {
    /** Nom de la colonne, correspondant au nom de la proprit contenant la valeur d'une cellule */
    keyColumn: string;

}

const logger: Logger = Utils.getLogger("hornet-js-react-components.widget.table.column.cell.body-cell");

/**
 * Classe permettant de gnrer le rendu html d'un cellule du corps d'un tableau
 */
export class BodyCell<P extends BodyCellProps, S> extends AbstractBodyCell<P, S> {

    /**
     * @inheritDoc
     */
    renderCell(): JSX.Element {
        logger.trace("render BodyCell -> column:", this.props.coordinates.column, " - line:",
            this.props.coordinates.row, "- isFocused:", this.state.isFocused, "- tabIndex:", this.state.tabIndex);
        return (
            this.state.value
        );
    }
}



