var Ajv = require("ajv");

/**
 *  Proprits d'une classe de validation customise d'un formulaire
 */
export interface ICustomValidation {

    /**
     * Mthode de validation customise d'un formulaire : mthode gnrique appele automatiquement depuis le composant
     * form.tsx si sa proprit customValidation est valorise
     * @param data donnes de formulaire
     * @return les rsultats de validation
     * */
    validate(data: any): IValidationResult;
};

/**
 * Rsultat de validation
 */
export interface IValidationResult {
    /** Indique si les donnes sont valides */
    valid: boolean;

    /** Tableau d'erreurs de validation ventuelles */
    errors: Array<ajv.ErrorObject>
};

/**
 * Contient tous les lments ncessaires  une validation de donnes
 */
export class DataValidator {

    /**
     * Options de validation ajv par dfaut, utilisables ct client et serveur (les dates sont supposes tre des
     * chanes de caractres au format ISO 8601)
     */
    static DEFAULT_VALIDATION_OPTIONS: ajv.Options = {
        /* Activation des mots cl json-schema v5 (https://github.com/json-schema/json-schema/wiki/v5-Proposals) */
        v5: true,
        /* Valide tous les champs : ne s'arrte pas  la premire erreur */
        allErrors: true,
        /* Convertit les chanes de caractres vers le type indiqu dans le schma de validation */
        coerceTypes: true,
        /* Prend en compte les valeurs par dfaut ventuellement prsentes dans le schma */
        useDefaults: true,
        /* Mode de validation complet : impacte les formats date, time, date-time, uri, email, et hostname.
        En mode 'full', les champs de format "email' sont valids en appliquant la RFC 5322. */
        format: "full"
    };

    /** Schma de validation au format json-schema */
    schema: any;

    /** Options de validation ajv (cf. http://epoberezkin.github.io/ajv/#options) */
    options: ajv.Options;

    /**
     * Valideurs customiss : permettent d'implmenter et de chaner des rgles de validation difficiles  mettre
     * en oeuvre simplement avec un schma json-schema. Ils sont appliqus aprs la validation base sur le schma
     * de validation, donc les donnes du formulaire ont dj ventuellement bnfici de la coercition de types. */
    customValidators: ICustomValidation[];

    constructor(schema?: any, customValidators?: ICustomValidation[], options = DataValidator.DEFAULT_VALIDATION_OPTIONS) {
        this.schema = schema;
        this.customValidators = customValidators;
        this.options = options;
    }

    /**
     * Excute la validation
     * @param data donnes  valider
     * @return {IValidationResult} rsultat de la validation
     */
    validate(data: any): IValidationResult {
        let result: IValidationResult = {
            valid: true,
            errors: []
        };
        if (this.schema) {
            let ajvInstance: ajv.Ajv = Ajv(this.options);
            require('ajv-keywords')(ajvInstance);
            result.valid = ajvInstance.validate(this.schema, data);
            result.errors = ajvInstance.errors || [];
        }

        /* Prise en compte des valideurs customiss ventuels */
        if (this.customValidators) {
            for (let index in this.customValidators) {
                if (this.customValidators[index] && (typeof this.customValidators[index] != "function")) {
                    let customResult: IValidationResult = this.customValidators[index].validate(data);

                    if (!customResult.valid && customResult.errors) {
                        result.errors = result.errors.concat(customResult.errors);
                    }
                    result.valid = result.valid && customResult.valid;
                }
            }
        }

        if (result.errors && Array.isArray(result.errors)) {
            for (let index in result.errors) {
                result.errors[index].dataPath = result.errors[index].dataPath.replace("'][", ".").replace("['", "").replace("]", "");
            }
        }

        return result;
    }

    /**
     * Transforme le schma de validation indiqu en un schma JSON-Schema valide. Dans le schma pass en paramtre,
     * le mot cl "required" peut-tre spcifi par champ de type string.
     * En sortie les noms champs obligatoires sont regroups dans un tableau, conformment  la spcification JSON-Schema
     * et le mot-cl "minLength" est utilis pour les champs obligatoires.
     * Exemple :
     * {
     *  "$schema": "http://json-schema.org/schema#",
     *  "type": "object",
     *  "properties": {
     *      "champ1": {"type": "string", "required": true},
     *      "champ2": {"type": "number"}
     *  }
     * }
     *
     * devient :
     * {
     *  "$schema": "http://json-schema.org/schema#",
     *  "type": "object",
     *  "properties": {
     *      "champ1": {"type": "string", "minLength": 1},
     *      "champ2": {"type": "number"}
     *  },
     *  "required": ["champ1"]
     * }
     *
     * @param hornetSchema schma de validation
     * @return un schma json-schema valide
     */
    static transformRequiredStrings(hornetSchema: any): any {
        var resultSchema: any;
        if (hornetSchema) {
            resultSchema = _.cloneDeep(hornetSchema);
            resultSchema.required = resultSchema.required || [];
            // TODO  appliquer rcursivement, chaque champ pouvant lui mme tre un objet
            for (var fn in resultSchema.properties) {
                var field = resultSchema.properties[fn];
                if (field.required === true && field.type == "string") {
                    field.minLength = 1;
                    if (resultSchema.required.indexOf(fn) == -1) {
                        resultSchema.required.push(fn);
                    }
                    delete field.required;
                }
            }
            /* Aucune proprit n'est requise : on supprime dans ce cas la proprit required pour tre compatible avec ajv */
            if (resultSchema.required.length == 0) {
                delete resultSchema.required;
            }
        }
        return resultSchema;
    }
}


