import * as _ from "lodash";

/**
 * Classe utilitaire sur les arrays.
 */
export class ArrayUtils {


    /**
     * retoure l'index de l'objet dans une liste via son ID
     * @param objectList
     * @param object
     * @returns {number}
     */
    static getIndexById(objectList: any[], object: any): number {
        let result: number = -1;
        result = object && objectList ? _.findIndex(objectList, (item) => {
            return item.id === object.id;
        }) : result;
        return result;
    }


    /**
     * fonction qui test si une liste d'objet est incluse dans une autre
     * @param objectList1
     * @param objectList2
     * @returns {boolean}
     */
    static isInclude(objectList1: any[], objectList2: any[]): boolean {
        if (objectList1 && objectList1.length === 0 && objectList2 && objectList2.length === 0) {
            return false;
        }
        let result = _.filter(objectList1, (item) => {
            let index = this.getIndexById(objectList2, item);
            return (index === -1 );
        });
        return result.length === 0;
    }

    /**
     * fonction qui fait l'union de deux liste avec le predicat id different
     * @param object
     * @param other
     *  @returns {any[]}
     */
    static unionWith(object: any[], other: any[]): any[] {
        let listResult: any[] = _.unionWith(object, other,
            (item1, item2) => {
                return item1.id === item2.id;
            });

        return listResult;
    }

    /**
     * retourne l'intersection de deux liste
     * @param object
     * @param other
     * @returns {any[]}
     */
    static intersectionWith(object: any[], other: any[]): any[] {
        let listResult: any[] = _.intersectionWith(object, other,
            (item1, item2) => {
                return item1.id === item2.id;
            });

        return listResult;
    }

}


