import * as events from "events";
import { CellCoordinates } from "src/widget/table/column/cell/cell-coordinates";
import * as _ from "lodash";
import { ColumnState } from "hornet-js-react-components/src/widget/table/column";

export class TableState extends events.EventEmitter {

    static INDEX_CHANGE_EVENT: string = "onIndexChange";
    static RESIZE_EVENT: string = "onResizeChange";

    protected contentsState: ContentState[] = [];
    protected index: number;

    addContent(contentState: ContentState) {
        contentState.on(ContentState.ITEMS_CHANGE_EVENT, this.setIndexAfterItemsChangeEvent.bind(this));
        this.contentsState.push(contentState);

    }

    removeContent(contentState: ContentState) {
        contentState.removeListener(ContentState.ITEMS_CHANGE_EVENT, this.setIndexAfterItemsChangeEvent.bind(this));
        _.remove(this.contentsState, contentState);
    }

    removeContents() {
        this.contentsState.map((contentState: ContentState) => this.removeContent(contentState));
    }

    setIndexAfterItemsChangeEvent(items) {
        this.contentsState.map((contentState, index) => {
            if (items === contentState.items) {
                this.index = index;
                this.emit(TableState.INDEX_CHANGE_EVENT,
                    null, this.contentsState[index].items
                );
            }
        });
    }

    getIndex() {
        return this.index;
    }

}

export class ContentState extends events.EventEmitter {

    static FOCUS_CHANGE_EVENT: string = "onFocusChange";
    static ITEMS_CHANGE_EVENT: string = "onItemsChange";
    static RESIZE_EVENT: string = "onResizeChange";
    static EDITION_CLIC_EVENT: string = "onEditionClic";
    static BLUR_EVENT: string = "onBlur";
    static TOGGLE_COLUMNS_EVENT: string = "toggleColumns";

    protected focusedCell: CellCoordinates;
    public items = [];
    public itemInEdition: any;
    public hiddenColumns: any = {};
    public firstVisibleColumnState: ColumnState;

    private oldFirstVisibleColumnState: ColumnState;

    public setFocusOn(focusedCell: CellCoordinates) {
        this.emit(ContentState.BLUR_EVENT, this.focusedCell);
        this.emit(ContentState.FOCUS_CHANGE_EVENT, this.focusedCell, focusedCell);
        this.focusedCell = focusedCell;
    }

    public setItemInEdition(itemInEditionValue: any, lineIndex: number) {
        this.itemInEdition = itemInEditionValue;
        if (this.itemInEdition) {
            this.itemInEdition["row"] = lineIndex;
        }
        this.emit(ContentState.EDITION_CLIC_EVENT, lineIndex);
    }

    public setItems(items: any[]) {
        this.items = items;
        this.emit(ContentState.ITEMS_CHANGE_EVENT, this.items);
    }

    public setHiddenColumns(hiddenColumns: any) {
        this.hiddenColumns = hiddenColumns;
        this.emit(ContentState.TOGGLE_COLUMNS_EVENT, this.hiddenColumns, this.firstVisibleColumnState, this.oldFirstVisibleColumnState);
    }

    public setFirstVisibleColumnState(columnState: ColumnState) {
        if (columnState != this.firstVisibleColumnState) {
            this.oldFirstVisibleColumnState = this.firstVisibleColumnState;
            this.firstVisibleColumnState = columnState;
        }
    }
}



