import { Utils } from "hornet-js-utils";
import { Logger } from "hornet-js-utils/src/logger";

import * as React from "react";


import {AbstractField, AbstractFieldProps} from "src/widget/form/abstract-field";
import {
    IHornetComponentDatasource,
    IHornetComponentAsync
} from "hornet-js-components/src/component/ihornet-component";
import { HornetComponent, HornetComponentDatasourceProps } from "src/widget/component/hornet-component";
import { DataSource } from "hornet-js-core/src/component/datasource/datasource";
import { SpinnerComponentInput } from "src/widget/spinner/spinner-component-input";
import { SpinnerProps } from "src/widget/spinner/spinner-component";

const logger: Logger = Utils.getLogger("hornet-js-react-components.widget.form.abstract-field-datasource");

/**
 * Reprsente un champ de formulaire qui possde un datasource
 */
export abstract class AbstractFieldDatasource<P extends HornetComponentDatasourceProps & AbstractFieldProps, S> extends AbstractField<HornetComponentDatasourceProps & AbstractFieldProps, S> implements IHornetComponentDatasource, IHornetComponentAsync {

    readonly props: Readonly <HornetComponentDatasourceProps & AbstractFieldProps>;

    constructor(props?: HornetComponentDatasourceProps & AbstractFieldProps, context?: any) {
        super(props, context);
    }

    /**
     * Mthode qui controle l'affichage et la suppression du spinner
     * @param flag boolen true pour l'afficher false sinon
     */
    displaySpinner(flag: boolean) {
        this.state.inProgress = flag;
        if (!this.props.hideSpinner) {
            this.refs.spinnerLoader && (this.refs.spinnerLoader as SpinnerComponentInput<SpinnerProps, SpinnerProps>).progress(flag);
            flag ? this.showSpinnerComponent() : this.hideSpinnerComponent();
        }
    }


    /**
     * @inheritDoc
     */
    componentWillMount() {
        super.componentWillMount();
        if (this.props.dataSource) {
            this.setItem();
        }
    }

    /**
     * @inheritDoc
     */
    componentDidMount() {
        super.componentDidMount();
        if (this.props.dataSource) {
            this.props.dataSource.on("fetch", this.setItem);
            this.props.dataSource.on("init", this.setItem);
            this.props.dataSource.on("add", this.setItem);
            this.props.dataSource.on("delete", this.setItem);
            this.props.dataSource.on("sort", this.setItem);
            this.props.dataSource.on("filter", this.setItem);
            this.props.dataSource.on("loadingData", this.displaySpinner);
        }
    }

    /**
     * @inheritDoc
     */
    componentWillUnmount() {
        super.componentWillUnmount();
        if (this.props.dataSource) {
            this.props.dataSource.removeListener("fetch", this.setItem);
            this.props.dataSource.removeListener("init", this.setItem);
            this.props.dataSource.removeListener("add", this.setItem);
            this.props.dataSource.removeListener("delete", this.setItem);
            this.props.dataSource.removeListener("sort", this.setItem);
            this.props.dataSource.removeListener("filter", this.setItem);
            this.props.dataSource.removeListener("loadingData", this.displaySpinner);
        }
    }

    /**
     * enregistre la liste des choix possibles
     */
    protected setItem() {
        this.setState({items: this.props.dataSource.results});
        /* permet de faire appel a la mthode setCurrentValue de dom-adapter pour cocher les valeurs*/
        this.setCurrentValue(this.state.currentValue);
    }

    /**
     * Mthode premettant d'afficher le spinner
     * @returns {Table}
     */
    showSpinnerComponent(): this {
        this.setState({spinner: true});
        return this;
    }

    /**
     * Mthode premettant de masquer le spinner
     * @returns {Table}
     */
    hideSpinnerComponent(): this {
        this.setState({spinner: false});
        return this;
    }

    public setDataSource(value: DataSource<any>, callback?: () => any) {
        /** liste des choix disponibles */
        this.setState({dataSource: value}, callback);
        return this;
    }


    public updateDataSource(value: any) {
        this.setDataSource(value);
    }

    /**
     * Gnre le rendu des lements assurant la saisie des valeurs
     * @returns {any}
     */
    renderField(): JSX.Element {
        return (
            <div className={this.state.fieldClass + " abstractfield-field-content"}>
                {<SpinnerComponentInput ref="spinnerLoader" isVisible={this.state.spinner}/>}
                {this.state.prefix ? <span className="abstractfield-field-prefix">{this.state.prefix}</span> : null}
                {this.renderWidget()}
                {this.state.suffix ? <span className="abstractfield-field-suffix">{this.state.suffix}</span> : null}
                {this.renderErrors()}
            </div>
        );
    }
}


