import { Utils } from "hornet-js-utils";
import { Logger } from "hornet-js-utils/src/logger";
import { AbstractBodyCell, AbstractBodyCellProps } from "src/widget/table/column/cell/abstract-body-cell";
import * as React from "react";
import { InputField } from "src/widget/form/input-field";

const logger: Logger = Utils.getLogger("hornet-js-react-components.widget.table.input-text-in-line-body-cell");

export interface InputTextInLineBodyCellProps extends AbstractBodyCellProps {
}

export class InputTextInLineBodyCell<P extends InputTextInLineBodyCellProps, S> extends AbstractBodyCell<P, any> {


    constructor(props: P, context?: any) {
        super(props, context);
        this.state.value = props.value[props.keyColumn];
    }

    shouldComponentUpdate(nextProps, nextState) {
        return super.shouldComponentUpdate(nextProps, nextState) || nextState.isEditing !== this.state.isEditing;
    }


    /**
     * @inheritDoc
     */
    renderCell() {
        logger.trace("render InputTextBodyCell-> column:", this.props.coordinates.column, " - line:", this.props.coordinates.row);

        let inputTextProps: any = {
            name: this.state.keyColumn,
            hideError : true,
            className: "table-cell-input",
            groupClass: "table-cell-container",
            type: "text",
            currentValue: this.state.value,
            title: this.state.title
        };

        return (
            !this.state.isEditing ?
                this.state.value : <InputField {...inputTextProps}/>
        );
    }

    /**
     * @inheritDoc
     */
    getCellTitle() {
        return !this.state.isEditing ? this.state.value : "";
    }


    /**
     * @inheritDoc
     */
    handleCellFocus(tableCellRef)  {
        if (tableCellRef ) {
            //lorsque la cellule n'est en cours d'edition, on met le focus sur toute la div
            if (!this.state.isEditing) {
                tableCellRef.focus();
            }
            //sinon , on met le focus sur le premier element HTML de type input
            else if (tableCellRef instanceof HTMLInputElement) {
                (tableCellRef as HTMLElement).focus();

            } else if (tableCellRef.children) {
                this.handleCellFocus(tableCellRef.children[0]);
            }
        }

    }



}


